<template>
  <div>
    <Navbar />
    <el-row style="margin: 2vh">
      <el-col :span="24">
        <h1 style="font-family: 'TmoneyRoundWindRegular';"> </h1>
        <div v-if="recommend.calory - diet.calory > 0" style="font-size:18px; font-family: 'TmoneyRoundWindRegular'">   {{ recommend.calory - diet.calory }}kcal .</div>
        <div v-else>   {{ diet.calory - recommend.calory }}kcal .</div>
      </el-col>
    </el-row>
    <el-row style="display: flex; justify-content: center">
      <el-col :span="20">
        <div v-for="(food, idx) in food_list" :key="idx" style="margin: 4vw">
          <div class="food-box" style=" font-family:'TmoneyRoundWindRegular'">
            {{ food }}
          </div>
        </div>
      </el-col>
    </el-row>
    <router-link :to="{ name: 'DietRecordCreate' }" class="text-decoration-none">
      <div id="submit" style="background-color: #ADEC6E; color: black; width: 100%; height: 50px; display:flex; align-items: center; justify-content: center; font-family: 'TmoneyRoundWindRegular'"> </div>
    </router-link>


  </div>
</template>

<script>
import axios from 'axios';
import SERVER from "@/api/drf.js"
import Navbar from "@/components/my_page/Navbar"

export default {
  name: "DietRecordDetail",
  components: {
    Navbar,
  },
  data: function () {
    return {
      // diet: {
      //   calory: "",
      //   carbohydrate: "", // 
      //   protein: "",
      //   fat: "",
      //   sodium: "", // 
      // }
      diet: {
        calory: "",
        carbohydrate: "", // 
        protein: "",
        fat: "",
        sodium: "", // 
      },
      recommend: {
        calory: "",
        carbohydrate: "", // 
        protein: "",
        fat: "",
        sodium: "", // 
      },
      colors: [
        {color: '#F57053', percentage: 1},
        {color: '#f56c6c', percentage: 20},
        {color: '#e6a23c', percentage: 40},
        {color: '#5cb87a', percentage: 60},
        {color: '#1989fa', percentage: 80},
        {color: '#ADEC6E', percentage: 100},
        {color: '#F57053', percentage: 101},
      ],
      food_list: [],
      values: [],
      today: "",
      year: "", // 
      month: "",  // 
      date: "", // 
    }
  },
  methods: {
    getDate() {
      let today = new Date();   

      let year = today.getFullYear(); // 
      let month = today.getMonth() + 1;  // 
      let date = today.getDate();  // 

      this.today = today
      this.year = ('0000' + year).slice(-4)
      this.month = ('00' + month).slice(-2)
      this.date = ('00' + date).slice(-2)
    },
    getDietRecord: function () {
      axios.get(`${SERVER.URL}${SERVER.ROUTES.getDietRecord}${this.$store.state.userId}`)
        .then((res) => {
          this.food_list = res.data
      })
        .catch(() => {
          alert('     .')
      })
    },
    getDietRecordToday: function () {
      axios.get(`${SERVER.URL}${SERVER.ROUTES.getDietRecordToday}?day=${this.year}${this.month}${this.date}&userId=${this.$store.state.userId}`)
        .then((res) => {
          this.diet.calory = res.data.calory
          this.recommend.calory = res.data.totalCalory
        })
        .catch(() => {
          alert('     .')
        })
    },

  },
  created () {
    this.getDate()

    //  , , 
    //    
    //        

    //      id  
    this.userId = this.$route.path.split('/')[3]

    //   localStorage 
    this.$store.commit("GET_USERID")
    this.myId = this.$store.state.userId

    //   
    if (this.userId == this.myId) {
      this.getDietRecord()
      this.getDietRecordToday()
    }
    //   
    else {
      alert("     .")
      this.$router.go(-1)
    }
  },
}
</script>

<style scoped>
  #submit {
    position: fixed;
    bottom: 0rem;
  }
  .food-box {
    border: #F3F3F3 3px solid;
    box-shadow: 1px 1px 1px 1px gray;
    border-radius: 5px;
    font-weight: bold;
    font-size: 18px;
  }
</style>